
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
data () {
return {
detail: {}
}
},
methods: {
formatePrice (num) {
return num / 100
},
getDetail (id) {
this.$tupHttp(this, 'front/account/cash/info/' + id).then(res => {
let detail = res.data.model
detail.payAmount = this.$util.formatThousand(this.formatePrice(detail.payAmount))
detail.orderAmount = this.$util.formatThousand(this.formatePrice(detail.orderAmount))
detail.availableLimit = this.$util.formatThousand(this.formatePrice(detail.availableLimit))
detail.commitTime = detail.commitTime ? new Date(detail.commitTime).format('yyyy-MM-dd hh:mm:ss') : detail.commitTime
detail.createTime = detail.createTime ? new Date(detail.createTime).format('yyyy-MM-dd hh:mm:ss') : detail.createTime
this.detail = detail
})
}
},
created () {
this.getDetail(this.$route.query.id)
},
components: {
XHeader
}
}



